@import 'src/common/styles/sass/mixins';
@import 'src/common/styles/sass/variables';

.contactsBlock {
  background-image: url("../assets/image/contacts.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;

  .contactsContainer {
    flex-direction: column;

    .contactForm {
      @include flexBox(flex, column, null, null, null);
      margin-bottom: 20px;
      width: 50%;

      .input {
        margin-bottom: 30px;
        border-radius: 22px;
        height: 30px;
        padding: 5px 25px;
        border: 1px solid #d9d9d9;
        background-color: #ffffff;
      }

      .textarea {
        min-height: 90px;
      }

      @media screen and (max-width: 394px) {
        width: 100%;
      }
    }

    button {
      @include text($titleFontFamily, 14px, 22px, white, 700);
      letter-spacing: 1.5px;
      text-transform: uppercase;
      padding: 0 45px;
      background-color: $primaryColor;
      border-radius: 29px;
      border: 3px solid $primaryColor;
      height: 60px;
      margin-right: 15px;
      position: relative;
      z-index: 1;

      &:hover {
        background-color: white;
        color: $primaryColor;
        border: 3px solid $primaryColor;
        border-radius: 29px;
      }
    }

  }
}







